/**
  Import widgets style
 */
.wpaw-term {
  display: inline-block;
  background-color: #000;
  color: #fff;
  line-height: 1;
  font-size: 12px;
  padding: 3px 6px 2px;
}

.wpaw-posts-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.wpaw-posts-list__item {
  margin: 0;
  padding: 0;
}

.wpaw-posts-list__item > a {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  text-decoration: none;
}

.wpaw-posts-list__figure {
  position: relative;
  overflow: hidden;
  margin-right: 1em;
  flex: none;
  height: 90px;
  width: 90px;
  background-color: #eee;
}

.wpaw-posts-list__figure > img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: auto;
  height: auto;
  min-height: 100%;
  min-width: 100%;
  transform: translate(-50%, -50%);
}

@supports (object-fit: cover) {
  .wpaw-posts-list__figure > img {
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 50% 50%;
    transform: none;
  }
}

.wpaw-posts-list__body {
  flex: 1 1 0%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}

.wpaw-posts-list__body > * {
  flex: none;
}

.wpaw-posts-list__date {
  margin-top: auto;
}

.wpaw-slider__canvas {
  visibility: hidden;
}

.wpaw-slider__canvas.slick-initialized {
  visibility: visible;
}

.wpaw-slider__mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.wpaw-slider__item {
  position: relative;
  display: block;
  text-decoration: none;
}

.wpaw-slider__item .wpaw-slider__figure {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.wpaw-slider__figure {
  position: relative;
  overflow: hidden;
  display: block;
  background-color: #111;
}

.wpaw-slider__figure > img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: auto;
  height: auto;
  min-height: 100%;
  min-width: 100%;
  transform: translate(-50%, -50%);
}

@supports (object-fit: cover) {
  .wpaw-slider__figure > img {
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 50% 50%;
    transform: none;
  }
}

.wpaw-slider__item-content {
  position: relative;
  color: #fff;
}

.wpaw-slider__item-more--ghost {
  border: 1px solid #fff;
  color: #fff;
}

.wpaw-slider .slick-list {
  height: auto !important;
}

.wpaw-slider .slick-track {
  display: flex;
}

.wpaw-slider .slick-slide > div > div {
  display: block !important;
}

